            </div>
            <div style="display: flex; flex-wrap: wrap">
                <div class="car-item-line">
                    车辆外观：<van-tag :type="it.FAppearance ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FAppearance == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FAppearance == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    驾驶舱：<van-tag :type="it.FCockpit ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FCockpit == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FCockpit == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    引擎盖内：<van-tag :type="it.FEngineHood ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FEngineHood == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FEngineHood == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    三检表：<van-tag :type="it.FMeter ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FMeter == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FMeter == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    灭火器检查表：<van-tag :type="it.FExtinguisher ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FExtinguisher == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FExtinguisher == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    车辆系统：<van-tag :type="it.FVehicleSystem ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FVehicleSystem == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FVehicleSystem == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    车辆轮胎：<van-tag :type="it.FTyre ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FTyre == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FTyre == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    车辆牌照及附件：<van-tag :type="it.FLicenseTag ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FLicenseTag == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FLicenseTag == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    车辆运行：<van-tag :type="it.FRunningStatus ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FRunningStatus == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FRunningStatus == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    驾驶舱摄像头1：<van-tag :type="it.FCamera1 ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FCamera1 == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FCamera1 == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    驾驶舱摄像头2：<van-tag :type="it.FCamera2 ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FCamera2 == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FCamera2 == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    车厢内摄像头：<van-tag :type="it.FCamera2 ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FCamera3 == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FCamera3 == 0" />
                    </van-tag>
                </div>
                <div class="car-item-line">
                    其他情况：<van-tag :type="it.FOthers ? 'primary' : 'warning'">
                        <van-icon name="success" size="medium" v-if="it.FOthers == 1" />
                        <van-icon name="cross" size="medium" v-if="it.FOthers == 0" />
                    </van-tag>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
    name: "CarsCheckDetail",
    data() {
        return {
            carsObj: [],
            index: this.$u.getItem("index"),
        };
    },
    methods: {
        manage() {
            this._api.VMS_Car_CheckManage().then((res) => {
                res.data.forEach((ele) => {
                    this.carsObj.push(ele.EntryList);
                });
            });
        },
    },
    created() {
        this.manage();
        let a = this.$route.params.index
        this.$u.setItem("index", a)
    },
};
</script>

<style lang="scss" scoped>
.main-page {
    .shadow {
        padding: 15px 15px;
    }

    .headerTag {
        margin-bottom: 20px;
    }

    .detail-wrap {
        width: 80%;
        margin: 0 auto;
    }
}
</style>
